.preloader {
  z-index: 10;
  background: url("prel-film.768dca65.jpg") center / cover no-repeat;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.preloader > .preloader-box {
  width: 345px;
  height: 30px;
  position: absolute;
  top: 50%;
  left: 50%;
}

.preloader .preloader-box > div {
  width: 30px;
  height: 30px;
  float: left;
  text-align: center;
  color: #fff;
  background: #ccc;
  border-radius: 5px;
  font-family: Verdana;
  font-size: 30px;
  line-height: 30px;
  position: relative;
}

.preloader .preloader-box > div:nth-child(1) {
  background: red;
  margin-right: 15px;
  animation: movement .6s infinite alternate;
}

.preloader .preloader-box > div:nth-child(2) {
  background: green;
  margin-right: 15px;
  animation: movement .6s .1s infinite alternate;
}

.preloader .preloader-box > div:nth-child(3) {
  background: #ff0;
  margin-right: 15px;
  animation: movement .6s .2s infinite alternate;
}

.preloader .preloader-box > div:nth-child(4) {
  background: #36f;
  margin-right: 15px;
  animation: movement .6s .3s infinite alternate;
}

.preloader .preloader-box > div:nth-child(5) {
  background: #ff4500;
  margin-right: 15px;
  animation: movement .6s .4s infinite alternate;
}

.preloader .preloader-box > div:nth-child(6) {
  background: #20b2aa;
  margin-right: 15px;
  animation: movement .6s .5s infinite alternate;
}

.preloader .preloader-box > div:nth-child(7) {
  background: #f08080;
  margin-right: 15px;
  animation: movement .6s .6s infinite alternate;
}

.preloader .preloader-box > div:nth-child(8) {
  background: #add8e6;
  animation: movement .6s .7s infinite alternate;
}

@keyframes movement {
  from {
    transform: scale(1)translateY(0)rotateX(0deg);
    box-shadow: 0 0 rgba(0, 0, 0, 0);
  }

  to {
    background: #8a2be2;
    transform: scale(1.5)translateY(-25px)rotateX(45deg);
    box-shadow: 0 25px 40px rgba(0, 0, 0, .4);
  }
}

