.button {
  color: #03e9f4;
  text-transform: uppercase;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid #03e9f4;
  margin-top: 80px;
  padding: 14px 10px;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.2;
  text-decoration: none;
  transition: all .5s;
  display: inline-block;
  position: relative;
  overflow: hidden;
}

.button:hover {
  color: #050801;
  -webkit-box-reflect: below 1px linear-gradient(transparent, rgba(0, 0, 0, .333));
  background: #03e9f4;
  box-shadow: 0 0 5px #03e9f4, 0 0 25px #03e9f4, 0 0 50px #03e9f4, 0 0 200px #03e9f4;
}

.button:nth-child(1) {
  filter: hue-rotate(270deg);
}

.button:nth-child(2) {
  filter: hue-rotate(110deg);
}

.button span {
  display: block;
  position: absolute;
}

.button span:nth-child(1) {
  width: 100%;
  height: 2px;
  background: linear-gradient(90deg, rgba(0, 0, 0, 0), #03e9f4);
  animation: animate1 3s linear infinite;
  top: 0;
  left: 0;
}

@keyframes animate1 {
  0% {
    left: -100%;
  }

  50%, 100% {
    left: 100%;
  }
}

.button span:nth-child(2) {
  width: 2px;
  height: 100%;
  background: linear-gradient(rgba(0, 0, 0, 0), #03e9f4);
  animation: animate2 3s linear .25s infinite;
  top: -100%;
  right: 0;
}

@keyframes animate2 {
  0% {
    top: -100%;
  }

  50%, 100% {
    top: 100%;
  }
}

.button span:nth-child(3) {
  width: 100%;
  height: 2px;
  background: linear-gradient(270deg, rgba(0, 0, 0, 0), #03e9f4);
  animation: animate3 3s linear .5s infinite;
  bottom: 0;
  right: 0;
}

@keyframes animate3 {
  0% {
    right: -100%;
  }

  50%, 100% {
    right: 100%;
  }
}

.button span:nth-child(4) {
  width: 2px;
  height: 100%;
  background: linear-gradient(360deg, rgba(0, 0, 0, 0), #03e9f4);
  animation: animate4 3s linear .75s infinite;
  bottom: -100%;
  left: 0;
}

@keyframes animate4 {
  0% {
    bottom: -100%;
  }

  50%, 100% {
    bottom: 100%;
  }
}

