@media screen and (min-width: 480px) {
  .modal_footer {
    z-index: 0;
    margin: 20px;
    padding: 2rem;
    position: relative;
    overflow: hidden;
  }

  .modal_footer:before {
    content: "";
    z-index: -2;
    width: 200%;
    height: 200%;
    background-color: #555151;
    background-image: conic-gradient(rgba(0, 0, 0, 0), #a8efff, rgba(0, 0, 0, 0) 30%);
    background-position: 0 0;
    background-repeat: no-repeat;
    animation: rotate 4s linear infinite;
    position: absolute;
    top: -50%;
    left: -50%;
  }

  .modal_footer:after {
    content: "";
    z-index: -1;
    background: #000;
    border-radius: 5px;
    position: absolute;
    top: 6px;
    left: 6px;
  }
}

@keyframes rotate {
  100% {
    transform: rotate(1turn);
  }
}

